/**
 * Docs color schemes
 */
[data-theme=light],
:root:not([data-theme=dark]) {
  --pico-card-component-outline-width: 0;
  --pico-card-component-outline-color: transparent;
  --pico-code-tag: #9c5900;
  --pico-code-attr: #aa3387;
  --pico-code-value: #327365;
  --pico-code-comment: #6f7887;
}

@media only screen and (prefers-color-scheme: dark) {
  :root:not([data-theme]) {
    --pico-card-component-outline-width: 2px;
    --pico-card-component-outline-color: var(--pico-card-sectionning-background-color);
    --pico-code-tag: #a08d52;
    --pico-code-attr: #b276ab;
    --pico-code-value: #669697;
    --pico-code-comment: #5c6370;
  }
}
[data-theme=dark] {
  --pico-card-component-outline-width: 2px;
  --pico-card-component-outline-color: var(--pico-card-sectionning-background-color);
  --pico-code-tag: #a08d52;
  --pico-code-attr: #b276ab;
  --pico-code-value: #669697;
  --pico-code-comment: #5c6370;
}

/**
 * Docs styles
 */
/**
 * Docs: Main
 */
@media (min-width: 992px) {
  body > main {
    grid-column-gap: 4rem;
    display: grid;
    grid-template-columns: 12rem auto;
  }
}

/**
 * Docs: Aside
 */
main > aside {
  --pico-font-size: 14px;
  font-size: var(--pico-font-size);
}
main > aside details summary {
  text-transform: uppercase;
}
main > aside details summary:after {
  display: none;
}
main > aside details[open] summary {
  margin-bottom: calc(var(--pico-spacing) * 0.75);
}
main > aside li {
  padding: 0;
}
main > aside li a {
  margin: 0;
  padding-top: calc(var(--pico-nav-element-spacing-vertical) * 0.25);
  padding-bottom: calc(var(--pico-nav-element-spacing-vertical) * 0.25);
}

/**
 * Docs: Document
 */
main [role=document] {
  min-width: 0;
}
main [role=document] article > *:last-child:not(footer, .grid),
main [role=document] article form > *:last-child:not(footer, .grid) {
  margin-bottom: 0;
}
main [role=document] h1:first-of-type {
  margin-top: -0.375em;
}
main [role=document]#themes article:first-of-type button {
  width: 100%;
}

/**
 * Docs: Sectionning
 */
main [role=document] article footer pre,
main [role=document] article footer pre code {
  margin-bottom: 0;
  padding: 0;
  background: transparent;
}
main [role=document] > .code pre,
main [role=document] hgroup {
  margin-bottom: var(--pico-block-spacing-vertical);
}
@media (min-width: 576px) {
  main [role=document] > .code pre,
  main [role=document] hgroup {
    --pico-block-spacing-horizontal: calc(var(--pico-spacing) * 1.25);
    --pico-block-spacing-vertical: calc(var(--pico-spacing) * 1.25);
  }
}
@media (min-width: 768px) {
  main [role=document] > .code pre,
  main [role=document] hgroup {
    --pico-block-spacing-horizontal: calc(var(--pico-spacing) * 1.5);
    --pico-block-spacing-vertical: calc(var(--pico-spacing) * 1.5);
  }
}
@media (min-width: 992px) {
  main [role=document] > .code pre,
  main [role=document] hgroup {
    --pico-block-spacing-horizontal: calc(var(--pico-spacing) * 1.75);
    --pico-block-spacing-vertical: calc(var(--pico-spacing) * 1.75);
  }
}
@media (min-width: 1200px) {
  main [role=document] > .code pre,
  main [role=document] hgroup {
    --pico-block-spacing-horizontal: calc(var(--pico-spacing) * 2);
    --pico-block-spacing-vertical: calc(var(--pico-spacing) * 2);
  }
}
main [role=document] > .code pre {
  padding: calc(var(--pico-block-spacing-vertical) * 0.75) var(--pico-block-spacing-horizontal);
  background-color: var(--pico-card-background-color);
  box-shadow: var(--pico-card-box-shadow);
}
main [role=document] > .code pre code {
  padding-right: 0;
  padding-left: 0;
  background: transparent;
}
main [role=document] > .code.small pre {
  padding: calc(var(--pico-block-spacing-vertical) * 0.25) var(--pico-block-spacing-horizontal);
}

/**
 * Docs: Code
 */
.token.tag:not(.punctuation), .token.property {
  color: var(--pico-code-tag);
}
.token.tag.attr-name:not(.punctuation), .token.selector:not(.punctuation), .token.keyword, .token.boolean, .token.function {
  color: var(--pico-code-attr);
}
.token.tag.attr-value:not(.attr-equals), .token.string {
  color: var(--pico-code-value);
}
.token.comment:not(.punctuation), .token.doctype {
  color: var(--pico-code-comment);
}

.code {
  position: relative;
}
.code > button {
  position: absolute;
  top: 1rem;
  right: 1rem;
  padding: 0.375rem 0.5rem;
  font-size: 14px;
  opacity: 0;
  transition: background-color var(--pico-transition), border-color var(--pico-transition), color var(--pico-transition), box-shadow var(--pico-transition), opacity var(--pico-transition);
}
.code:hover button {
  opacity: 1;
}

.code.small > button {
  top: 50%;
  transform: translateY(-50%);
}

p > code {
  color: inherit;
}

article footer:has(.code) {
  position: relative;
}
article footer:has(.code):hover button {
  opacity: 1;
}
article footer:has(.code) .code {
  position: initial;
}

/**
 * Docs: Card
 */
article.component {
  outline-color: var(--pico-card-component-outline-color);
  outline-style: solid;
  outline-width: var(--pico-card-component-outline-width);
  background-color: transparent;
}

article.color-picker > header {
  padding: 0;
  display: grid;
  grid-template-columns: repeat(20, 1fr);
  border-top-left-radius: var(--pico-border-radius);
  border-top-right-radius: var(--pico-border-radius);
  overflow: hidden;
}
article.color-picker > header a,
article.color-picker > header a:is(:hover, :active, :focus) {
  margin-bottom: 0;
  box-shadow: none;
  border: none;
  border-radius: 0;
  padding: 0;
  aspect-ratio: 1/1;
}
article.color-picker h3 {
  color: var(--pico-primary);
}

